import { IDataGridColumnDefinition } from './../../../../interfaces/data-grid.interface';
import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'siu-data-grid-v2-head-column',
  templateUrl: './data-grid-v2-head-column.component.html',
  styleUrls: [ './data-grid-v2-head-column.component.scss' ]
})
export class DataGridV2HeadColumnComponent implements OnInit {

  @Input() orderField: string;
  @Input() orderDir: string;
  @Input() colDef: IDataGridColumnDefinition;
  @Output('on-sort') onSortEvent: EventEmitter<any> = new EventEmitter();
  @Input() disabled: boolean = false;
  @Input() template: any;

  hover: boolean = false;
  constructor() { }

  ngOnInit() {

  }

  handleClickSort = () => {
    if (this.disabled) return false;
    this.orderDir = this.orderField == this.colDef.field ? (this.orderDir == 'asc' ? 'desc' : 'asc') : 'asc';
    const event = { direction: this.orderDir, active: this.colDef.field }
    this.onSortEvent.emit(event);
  }

}
