import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';

@Component({
  selector: 'siu-button-exportar',
  templateUrl: './button-exportar.component.html',
  styleUrls: [ './button-exportar.component.scss' ]
})
export class ButtonExportarComponent {

  @Input() label: string = 'Exportar';
  @Input() disabled: boolean = false;
  @Output('on-click') onClickEvent: EventEmitter<any> = new EventEmitter();
  @Input() buttonType: string = 'button';

  constructor() { }

  handleClick = () => {
    this.onClickEvent.emit();
  }

}
