import { IMsgValidations } from '@siu/shared';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Component, OnInit } from '@angular/core';
import { ValidateFormFields } from '@siu/shared';

@Component({
  selector: 'app-forms',
  templateUrl: './forms.component.html',
  styleUrls: [ './forms.component.scss' ]
})
export class FormsComponent implements OnInit {

  form: FormGroup;

  msgValidations: {
    nombres: IMsgValidations[],
    apellidos: IMsgValidations[],
    nacimiento: IMsgValidations[],
    pais: IMsgValidations[],
    email: IMsgValidations[],
    telefono: IMsgValidations[],
  };

  constructor(private formBuilder: FormBuilder) {

  }

  ngOnInit() {
    this.buildForm();
    this.buildValidations();
  }

  buildForm = () => {
    this.form = this.formBuilder.group({
      form1: this.formBuilder.group({
        nombres: [ null, [ Validators.required ] ],
        apellidos: [ null, [ Validators.required ] ],
      }),
      form2: this.formBuilder.group({
        nacimiento: [ null, [ Validators.required ] ],
        pais: [ null, [ Validators.required ] ]
      }),

      form3: this.formBuilder.group({
        email: [ null, [ Validators.required, Validators.email ] ],
        telefono: [ null, [ Validators.required ] ]
      })

    });
  }

  buildValidations = () => {
    this.msgValidations = {
      nombres: [
        { name: 'required', message: 'El campo es requerido' }
      ],
      apellidos: [
        { name: 'required', message: 'El campo es requerido' }
      ],
      nacimiento: [
        { name: 'required', message: 'El campo es requerido' }
      ],
      pais: [
        { name: 'required', message: 'El campo es requerido' }
      ],
      email: [
        { name: 'required', message: 'El campo es requerido' },
        { name: 'email', message: 'El email no es válido' }
      ],
      telefono: [
        { name: 'required', message: 'El campo es requerido' }
      ]
    };
  }

  handleSubmit = () => {

    ValidateFormFields(this.form);

    if (!this.form.valid) {
      return;
    }

  }


}
