import { FormGroup, FormBuilder } from '@angular/forms';
import update from 'immutability-helper';
import { JsonPlaceholderService } from './../../../services/json-placeholder.service';
import { map, distinctUntilChanged } from 'rxjs/operators';
import { Component, OnInit } from '@angular/core';
import { IDataGridEvent, IDataGridButtonEvent, IDataGridEditableSaveEvent, IDataGridEditableDefinition, IDataGridSource } from '@siu/shared';
import { TablasStore } from '../store/tablas.store';

@Component({
  selector: 'app-tablas',
  templateUrl: './tablas.component.html',
  styleUrls: [ './tablas.component.scss' ],
  providers: [ TablasStore ]
})
export class TablasComponent implements OnInit {

  readonly state$ = this.store.state$.pipe(map(state => state));

  constructor(private store: TablasStore, private jsonService: JsonPlaceholderService) {
  }

  ngOnInit() {
    this.store.asyncActionFetchPersons();
  }

  handleLoadData = (e: IDataGridEvent) => {
    this.store.asyncActionFetchPersons({
      page: e.page,
      pageSize: e.pageSize,
      orderBy: e.orderBy,
      orderDir: e.orderDir
    });
  }



  handleClickButton = (e: IDataGridButtonEvent) => {
    switch (e.action) {
      case 'CONSULTAR':
        alert(`click en consultar fila: ${e.index}`);
        break;

      case 'EDITAR':
        alert(`click en editar fila: ${e.index}`);
        break;

      case 'ELIMINAR':
        alert(`click en eliminar fila: ${e.index}`);
        break;
    }
  }

  handleLoadDataPersonsEditable = (e: IDataGridEvent) => {
  }

  handleSaveItemEditable = (e: IDataGridEditableSaveEvent) => {
    console.log(e.newValue);
    this.gridEditableSource.items = update(this.gridEditableSource.items,
      { [ e.rowIndex ]: { $set: e.newValue } }
    );

  }

  gridEditableDefinition: IDataGridEditableDefinition = {
    columns: [
      {
        label: 'Nombre', field: 'name', editable: true, controlType: 'select',
        selectConfig: {
          field: 'id',
          items: (item) => this.jsonService.fetchUsers(),
          bindLabel: 'name',
          bindValue: 'id',
          onChange: (item, selected) => { item.name = selected.name; item.postId = null; },
          triggerOnChange: [ 'postId' ]
        },
      },
      { label: 'Apellidos', field: 'surname', editable: true, controlType: 'text' },
      { label: 'Email', field: 'email', editable: true, controlType: 'text' },
      {
        label: 'Age',
        field: 'age',
        editable: true,
        controlType: 'text'
      },
      {
        label: 'Post', field: 'postId', editable: true, controlType: 'select',
        selectConfig: {
          field: 'postId',
          items: (item) => this.jsonService.fetchPosts(item.id),
          bindLabel: 'title',
          bindValue: 'id',
        }
      }
    ]
  }

  gridEditableSource: IDataGridSource<any> = {
    items: [
      {
        id: 1,
        name: 'Acevedo Adams',
        surname: 'Molina Galloway',
        email: 'adamsgalloway@frosnex.com',
        age: 24,
        postId: 1
      },
      {
        id: 2,
        name: 'Allen Robyn',
        surname: 'Alford Hendricks',
        email: 'robynhendricks@frosnex.com',
        age: 25,
        postId: 1
      },
      {
        id: 3,
        name: 'Ball Martin',
        surname: 'Chaney Sheppard',
        email: 'adamsgalloway@frosnex.com',
        age: 26,
        postId: 2
      },
      {
        id: 4,
        name: 'Best Welch',
        surname: 'Ramirez Wolf',
        email: 'welchwolf@frosnex.com',
        age: 27,
        postId: 3
      }
    ],
    page: 1,
    pageSize: 10,
    total: 1,
    orderBy: null,
    orderDir: null
  }

}
