import { EventEmitter, Output } from '@angular/core';
import { IDataGridColumnDefinition, IDataGridButtonEvent } from './../../../../interfaces/data-grid.interface';
import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'siu-data-grid-body-column',
  templateUrl: './data-grid-body-column.component.html',
  styleUrls: [ './data-grid-body-column.component.scss' ]
})
export class DataGridBodyColumnComponent implements OnInit {


  @Input() colDef: IDataGridColumnDefinition;
  @Input() element: any;
  @Input() template: any;
  @Input() rowIndex: number;
  @Input() loading: boolean;
  @Input() itemEditable: any;
  @Input() editing: boolean = false;

  datetimeDefaultFormat: string = 'DD/MM/YYYY HH:mm:ss';


  constructor() { }

  ngOnInit() {

  }

  getProperty = (obj, path) => {
    return path.split(/(\[|\]|\.)/).reduce((x, y) => {
      return "[].".indexOf(y) > -1 ? x : x === Object(x) && y in x ? x[ y ] : undefined;
    }, obj);
  };
}
