import { Injectable } from '@angular/core';
import { MatSnackBar, MatSnackBarConfig } from '@angular/material';

interface GenericMessage {
  type: number;
  hasErrors: boolean;
  hasWarnings: boolean;
  messages: any[];
}
export enum GenericMessageType {
  Info = 1,
  Warning = 2,
  Error = 3,
}

@Injectable({
  providedIn: 'root',
})
export class ToastService {

  constructor(private snackBar: MatSnackBar) {
  }

  success = (msg, closeLabel = 'Cerrar', config: MatSnackBarConfig = null) => {
    this.snackBar.open(msg, closeLabel, { panelClass: 'success', duration: 4000, ...config });
  }

  warning = (msg, closeLabel = 'Cerrar', config: MatSnackBarConfig = null) => {
    this.snackBar.open(msg, closeLabel, { panelClass: 'warning', duration: 4000, ...config });
  }

  error = (msg, closeLabel = 'Cerrar', config: MatSnackBarConfig = null) => {
    this.snackBar.open(msg, closeLabel, { panelClass: 'error', duration: 4000, ...config });
  }

  info = (msg, closeLabel = 'Cerrar', config: MatSnackBarConfig = null) => {
    this.snackBar.open(msg, closeLabel, { panelClass: 'info', duration: 4000, ...config });
  }

  show = (resp: GenericMessage) => {
    if (resp.hasErrors) {
      this.snackBar.open(resp.messages.map(x => x.message).join(','), 'Cerrar', { panelClass: 'error' });
    } else if (resp.hasWarnings) {
      this.snackBar.open(resp.messages.map(x => x.message).join(','), 'Cerrar', { panelClass: 'warning' });
    } else {
      this.snackBar.open(resp.messages.map(x => x.message).join(','), 'Cerrar', { panelClass: 'info' });
    }
  }

}
