import { EventEmitter, Output } from '@angular/core';
import {
  IDataGridColumnDefinition,
  IDataGridButtonEvent
} from './../../../../interfaces/data-grid.interface';
import { Component, OnInit, Input } from '@angular/core';

enum EnumColumnType {
  TEXT = 'TEXT',
  DATETIME = 'DATETIME',
  BUTTON = 'BUTTON',
  CUSTOM_TEMPLATE = 'CUSTOM-TEMPLATE'
}

@Component({
  selector: 'siu-data-grid-v2-body-column',
  templateUrl: './data-grid-v2-body-column.component.html',
  styleUrls: ['./data-grid-v2-body-column.component.scss']
})
export class DataGridV2BodyColumnComponent implements OnInit {
  enumColumnType = EnumColumnType;
  @Input() colDef: IDataGridColumnDefinition;
  @Input() element: any;
  @Input() template: any;
  @Input() rowIndex: number;
  @Input() loading: boolean;
  @Input() isLastColumn: boolean;
  @Input() isEditableGrid: boolean;
  @Input() editableOptions: any;
  @Output('on-click-button') onClickButtonEvent: EventEmitter<
    IDataGridButtonEvent
  > = new EventEmitter();
  @Output('on-edit') onEditEvent: EventEmitter<any> = new EventEmitter();
  @Input() expanded: boolean;
  @Input() openExpandable: () => void;
  @Input() closeExpandable: () => void;

  datetimeDefaultFormat: string = 'DD/MM/YYYY HH:mm:ss';

  columnType: EnumColumnType;

  constructor() {}

  ngOnInit() {
    this.setColumnType();
  }

  getProperty = (obj, path) => {
    return path.split(/(\[|\]|\.)/).reduce((x, y) => {
      return '[].'.indexOf(y) > -1
        ? x
        : x === Object(x) && y in x
        ? x[y]
        : undefined;
    }, obj);
  };

  setColumnType = () => {
    if (this.colDef.template) {
      this.columnType = this.enumColumnType.CUSTOM_TEMPLATE;
    } else if (this.colDef.buttons) {
      this.columnType = this.enumColumnType.BUTTON;
    } else if (this.colDef.isDatetime) {
      this.columnType = this.enumColumnType.DATETIME;
    } else {
      this.columnType = this.enumColumnType.TEXT;
    }
  };

  handleClickButton = (event, item, index) => {
    this.onClickButtonEvent.emit({ item, action: event.action, index });
  };

  handleClickEdit = () => {
    this.onEditEvent.emit();
  };
}
