import {
  IDataGridEditableDefinition,
  IDataGridSource,
  IDataGridDefinition,
  IDataGridColumnDefinition,
  IDataGridEvent,
  IDataGridButtonEvent,
  IDataGridEditableSaveEvent
} from './../../../interfaces/data-grid.interface';
import { Component, OnInit, Input, Output, EventEmitter, OnChanges, SimpleChanges, OnDestroy } from '@angular/core';
import { BuildGridButton } from '../../../functions/grid-buttons';
import { Observable, Subscription } from 'rxjs';

@Component({
  selector: 'siu-data-grid-editable',
  templateUrl: './data-grid-editable.component.html',
  styleUrls: [ './data-grid-editable.component.sass' ]
})
export class DataGridEditableComponent implements OnInit, OnChanges, OnDestroy {

  @Input() loading: boolean;
  @Input() definition: IDataGridEditableDefinition;
  @Input() source: IDataGridSource<any>;
  @Output('on-load-data') onLoadDataEvent: EventEmitter<IDataGridEvent> = new EventEmitter();
  @Output('on-save') onSaveDataEvent: EventEmitter<IDataGridEditableSaveEvent> = new EventEmitter();

  gridDefinition: IDataGridDefinition;
  gridSource: IDataGridSource<any>;

  tempValues: [];

  displayedColumns: string[];

  rowsValues: { value: any, edit: boolean, options?: any }[];

  selectOptions = {};

  subscriptions: Subscription[] = [];

  workData: any = {};

  constructor() {
  }

  ngOnInit() {
    /*this.definition.columns.filter(x => x.selectOptions).forEach(x => {
      this.selectOptions[ x.field ] = [ ...x.selectOptions ];
    });*/

    const columns = this.definition.columns.map((x): IDataGridColumnDefinition => {
      return {
        label: x.label,
        field: x.field,
        template: 'template-' + x.field
      };
    });

    columns.push({
      label: 'Acciones',
      field: 'buttons-editable',
      buttons: [
        BuildGridButton.EDITAR(null, (item, rowIndex) => this.workData[ rowIndex ]),
        BuildGridButton.GUARDAR(null, (item, rowIndex) => !this.workData[ rowIndex ]),
        BuildGridButton.CANCELAR(null, (item, rowIndex) => !this.workData[ rowIndex ])
      ],

    });

    this.gridDefinition = {
      columns: [
        ...columns
      ]
    }

    this.displayedColumns = this.gridDefinition.columns.map(x => x.field);
  }

  ngOnChanges(changes: SimpleChanges): void {
    /*if (changes.source) {
      this.rowsValues = this.source.items.map(x => ({
        edit: false,
        value: JSON.parse(JSON.stringify(x)),
        options: []
      }));
    }*/
  }

  ngOnDestroy(): void {
    this.subscriptions.forEach(x => {
      x.unsubscribe();
    });
  }

  handleLoadData = (e: IDataGridEvent) => {
    this.onLoadDataEvent.emit(e);
  }

  handleClickButton = (e: IDataGridButtonEvent) => {
    switch (e.action) {
      case 'EDITAR':
        const model = JSON.parse(JSON.stringify(e.item));
        this.workData[ e.index ] = { model: model, options: {}, selectConfig: {} };

        this.definition.columns.filter(x => x.selectConfig != null).map(col => {
          this.workData[ e.index ].selectConfig[ col.field ] = { ...col.selectConfig };
          const sub = this.workData[ e.index ].selectConfig[ col.field ].items(e.item).subscribe(data => {
            this.workData[ e.index ].options[ col.field ] = data;
          });
          this.subscriptions.push(sub);
        });

        /*
                this.rowsValues[ e.index ].edit = true;
                this.rowsValues[ e.index ].options = {};
                this.definition.columns.filter(x => x.selectOptions).map(col => {
                  if (Array.isArray(col.selectOptions)) {
                    this.rowsValues[ e.index ].options[ col.field ] = col.selectOptions;
                  } else {
                    const sub = col.selectOptions(this.rowsValues[ e.index ].value).subscribe(data => {
                      this.rowsValues[ e.index ].options[ col.field ] = data;
                    });
                    this.subscriptions.push(sub);
                  }
                });
        */
        break;

      case 'CANCELAR':
        this.workData[ e.index ] = null;
        break;

      case 'GUARDAR':
        this.handleSave(this.workData[ e.index ].model, e.item, e.index);
        this.workData[ e.index ] = null;
        break;
    }
  }

  handleSave = (newValue, oldValue, index) => {
    this.onSaveDataEvent.emit({
      newValue: newValue,
      oldValue: oldValue,
      rowIndex: index
    });
  }

  handleChangeRowSelect = (field, index) => {
    /*
        const colDefArray = this.definition.columns.filter(x => x.field == field);
        if (colDefArray.length > 0) {
          const colDef = colDefArray[ 0 ];
          if (typeof colDef.onChange === 'function') {
            colDef.onChange(this.rowsValues[ index ].value, (fieldName, newValue) => {
              this.rowsValues[ index ].options[ fieldName ] = newValue;
            });
          }
        }*/

    if (typeof this.workData[ index ].selectConfig[ field ].onChange === 'function') {

      const fieldSelectModel = this.workData[ index ].selectConfig[ field ].field;
      const selectedValue = this.workData[ index ].model[ fieldSelectModel ];

      const results = this.workData[ index ].options[ field ]
        .filter(x => x[ this.workData[ index ].selectConfig[ field ].bindValue || 'value' ] === selectedValue);

      const selectedObject = results.length > 0 ? results[ 0 ] : null;

      this.workData[ index ].selectConfig[ field ].onChange(this.workData[ index ].model, selectedObject);
    }

    if (this.workData[ index ].selectConfig[ field ].triggerOnChange) {
      this.workData[ index ].selectConfig[ field ].triggerOnChange.forEach(fieldToTrigger => {
        const sub = this.workData[ index ].selectConfig[ fieldToTrigger ]
          .items(this.workData[ index ].model).subscribe(data => {
            this.workData[ index ].options[ fieldToTrigger ] = data;
          });
        this.subscriptions.push(sub);
      });
    }

  }
}
