import { Pipe, PipeTransform } from "@angular/core";

@Pipe({
  name: "textLimit"
})
export class TextLimitPipe implements PipeTransform {

  transform(value: string, limit: number = 150): any {
    if (!value) {
      return value;
    }

    if (value.length > limit) {
      return value.substring(0, limit) + '...';
    }

    return value;
  }

}
