import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

const routes: Routes = [
  { path: '', loadChildren: './modules/01-home/home.module#HomeModule' },
  { path: 'demo', loadChildren: './modules/00-demo/demo.module#DemoModule' },
  { path: 'autorizaciones', loadChildren: './modules/autorizaciones/autorizaciones.module#AutorizacionesModule' },
  { path: '**', redirectTo: '', pathMatch: 'full' },
];

@NgModule({
  imports: [ RouterModule.forRoot(routes) ],
  exports: [ RouterModule ]
})
export class AppRoutingModule { }
