import { Router, NavigationEnd, RouterEvent, NavigationStart, NavigationCancel, NavigationError } from '@angular/router';
import { Component, OnDestroy, OnInit, ChangeDetectorRef } from '@angular/core';
import { AppStore } from './core/store/app.store';
import { map, distinctUntilChanged } from 'rxjs/operators';
import { MediaMatcher } from '@angular/cdk/layout';
import { MultilevelMenuService } from '@siu/shared';
import { Subscription } from 'rxjs';
//import createGuest from 'cross-domain-storage/guest';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html'
})
export class AppComponent implements OnInit, OnDestroy {

  //state: IAppStoreModel;

  //subscription: Subscription;

  loadingRoute: boolean = false;

  mobileQuery: MediaQueryList;

  private _mobileQueryListener: () => void;

  readonly state$ = this.store.state$.pipe(map(state => state), distinctUntilChanged());

  private subscriptions: Subscription[] = [];

  constructor(
    private store: AppStore,
    public router: Router,
    changeDetectorRef: ChangeDetectorRef,
    media: MediaMatcher,
    private multilevelMenuService: MultilevelMenuService

  ) {
    this.mobileQuery = media.matchMedia('(max-width: 600px)');
    this._mobileQueryListener = () => {
      changeDetectorRef.detectChanges();
      this.store.setIsMobile(this.mobileQuery.matches);
      if(this.mobileQuery.matches) {
        this.multilevelMenuService.expandFalse();
      } else {
        this.multilevelMenuService.expandTrue();
      }
      
    };
    this.mobileQuery.addListener(this._mobileQueryListener);
    this.multilevelMenuService.expandFalse();
   
    this.multilevelMenuService.exand_menu.subscribe(
      exandvalue => {
        if (exandvalue) {this.store.openDrawer();}
      }
    );
  }

  ngOnInit(): void {
 
    this.store.setIsMobile(this.mobileQuery.matches);
    this.subscribeToSession();
    this.subscribeToRouter();



   /* console.log('iniciando');
    var bazStorage = createGuest('http://localhost:3001');

     console.log(bazStorage);*/
    /* bazStorage.set('foo', 'bar nuevo 2', function (error, data) {
       console.log('guardo');

       console.log(error);
       console.log(data);
     });*/

    /* bazStorage.get('foo', function (error, value) {
       console.log(value);
       // value for the key of 'fizz' will be retrieved from localStorage on www.baz.com
     });
 */

this.multilevelMenuService.expandFalse();
  }

  ngOnDestroy() {
    this.mobileQuery.removeListener(this._mobileQueryListener);
    this.unSubscribe();
  }

  handleToogleDrawer = (open) => {
    if (open) {

      this.multilevelMenuService.expandTrue(); // menu expandido
      if (this.mobileQuery.matches) {
        this.store.openDrawerMobile();
      } else {
        this.store.openDrawer();
      }
    } else {
      this.multilevelMenuService.expandFalse();
      if (this.mobileQuery.matches) {
        this.store.closeDrawerMobile();
      } else {
        this.store.closeDrawer();
      }
    }
  }

  subscribeToSession = () => {
    const sub = this.store.state$
      .pipe(map(x => x.session), distinctUntilChanged())
      .subscribe(session => {
        if (!session.isLoggedIn) {
          console.log('no esta en sesion');
        }
      });
    this.subscriptions.push(sub);
  }

  unSubscribe = () => {
    this.subscriptions.forEach(x => {
      x.unsubscribe();
    });
  }

  subscribeToRouter = () => {
    this.router.events.subscribe((event: RouterEvent) => {
      this.navigationInterceptor(event)
    })
  }

  navigationInterceptor(event: RouterEvent): void {
    if (event instanceof NavigationStart) {
      this.loadingRoute = true
    }
    if (event instanceof NavigationEnd) {
      this.loadingRoute = false
    }
    // Set loading state to false in both of the below events to hide the spinner in case a request fails
    if (event instanceof NavigationCancel) {
      this.loadingRoute = false
    }
    if (event instanceof NavigationError) {
      this.loadingRoute = false
    }
  }

}
