import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { IAppTitle } from '../../../interfaces';
import { IUser } from '../../../interfaces/user.interface';

@Component({
  selector: 'siu-header',
  templateUrl: './header.component.html',
  styleUrls: [ './header.component.scss' ]
})
export class HeaderComponent implements OnInit {
  @Input() user: IUser;
  @Input() isMobile: boolean;
  @Input() title: IAppTitle;
  @Output('on-toogle-drawer') onToogleDrawerEvent: EventEmitter<any> = new EventEmitter<any>();
  @Output('on-logout') onLogoutEvent: EventEmitter<any> = new EventEmitter<any>();
  @Output('on-click-logo') onClickLogoEvent: EventEmitter<any> = new EventEmitter<any>();
  @Input() showToogleButton: boolean = false;
  @Output('on-click-change-password') onClickChangePasswordEvent: EventEmitter<any> = new EventEmitter<any>();
  constructor() { }

  ngOnInit() {

  }

  handleToogleDrawer = () => {
    this.onToogleDrawerEvent.emit();
  }

  handleLogout = () => {
    this.onLogoutEvent.emit();
  }

  handleClickLogo = () => {
    this.onClickLogoEvent.emit();
  }

  handleClickChangePassword = () => {
    this.onClickChangePasswordEvent.emit();
  }

}
